.circle {
  border-radius: 50%;
  border: 2px solid #6F7278;
  background-color: #46484C;
  color: #6F7278;
  cursor: pointer;
  position: absolute;
  z-index: 1000;
  text-align: center;
  text-decoration: none;
}

.circle a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #6F7278;
  text-decoration: none;
  width: 100%;
  height: 100%;
}